
import { Component, createRef } from 'react';
import '../css/horizontalBadge.scss';

export default class HorizontalBadge extends Component {
    constructor(props) {
        super(props);
        this.turnRef = createRef();
        this.ulRef = createRef();
        this.animateSection = this.animateSection.bind(this);
        this.reAnimateSection = this.reAnimateSection.bind(this);
    }

    animateSection() {
        this.turnRef.current.classList.add('hover')
    }

    reAnimateSection() {
        this.turnRef.current.classList.remove('hover')
    }
    render() {
        let rv = "", right = 'animate__slideInRight', left = 'animate__slideInLeft';
        if (this.props.index % 2 === 0) {
            rv = "flx-reverse";
            right = 'animate__slideInLeft';
            left = 'animate__slideInRight';
        }
        return (
            <div className="hbadge-stage wow animate__animated animate_rotatequick" data-wow-delay='1s'
                onMouseEnter={this.animateSection}
                onMouseLeave={this.reAnimateSection}
            >
                <div className={'row hbadge-info ' + rv}>
                    <div className={"hbadge-section col-md-4 " + rv + " wow " + left} data-wow-delay="0.2s" >
                        <div className={"hbadge-e-stage hbadge-circle-item item-" + this.props.index} ref={this.turnRef}>
                            <p>
                                {this.props.data.stage}
                                <br />
                            </p>
                        </div>
                        <div className="hbadge-title">
                            <div className="hbadge-position">
                                {this.props.data.position}
                            </div>
                            <div className="hbadge-company">
                                {this.props.data.subjectline}
                            </div>
                        </div>
                    </div>
                    <div className={"hbadge-acvm col-md-8 wow " + right} data-wow-delay="0.2s" >
                        <ul >
                            {this.props.data.description}
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}