import Section from '../../assets/components/section';
import staticData from './data/staticData.json';
import './css/educations.scss';
import HorizontalBadgeNoFlip from '../../assets/components/badgeHorizontalNoFlip'
import dynamicData from './data/dynamicData.js'


class Education extends Section {
    setData() {
        return staticData
    }
    getRawParameter(params) {
        return super.getRawParameter(this.setData())
    }
    prepareRenderingElementContent() {
        this.dynamicData = dynamicData;
        if (this.dynamicData.length === 0){
            this.dynamicData = []
        }
        return (
            <div className="container-fluid " >
                {
                    this.dynamicData.map((el, index) => {
                        return <HorizontalBadgeNoFlip key={index} index={index + 1} data={el} />
                    })
                }
        </div>
        )
    }
}

export default Education;
