import { Component } from 'react';
import { Animated } from "react-animated-css";
import intro from '../../assets/data/introduction.json' 
import ElementTextRepeat from '../../../../utils/components/elementTextRepeat.js'
import './css/intro.scss'
import background from '../../assets/img/background-img.jpg'

class Introduction extends Component{

    copyEmailAddress() {
        let p = document.createElement('input');
        p.id = intro.copyID;
        p.value = intro.email;
        document.body.appendChild(p)
        var copyText = document.getElementById(intro.copyID);
        copyText.select();
        copyText.setSelectionRange(0, 50);
        document.execCommand("copy");
        document.body.removeChild(p)
        document.getElementById('gmail-alert').style.display = 'block';
        setTimeout(() => {
            document.getElementById('gmail-alert').style.display = 'none';
        }, 5000)
    }

    
    componentDidMount() {
        this.pageSize = window.innerHeight
        this.pageHeight = document.body.scrollHeight
        window.addEventListener('scroll', e => {
            if (window.scrollY < this.pageSize)
                document.getElementById('bkg-banner-id').style.backgroundPosition = "50% -" + parseInt(window.scrollY / 2) + "px"
        })
    }

    render(){
        return (
            <div id='bkg-banner-id' className="bkg-banner" onScroll={this.onScrollFunction} style={{ 'backgroundImage': 'url(' + background + ')' }}>
                <div className="bkg-layer"></div>
                <div className="color-bkg">
                    <div className="d-flex justify-content-center" style={{ width: "100%", height: "100%" }}>
                        <div>
                            <div className="title">
                                <Animated animationIn="zoomInDown">
                                    <h2 className="display-2s text-center">{intro.name}</h2>
                                </Animated>
                            </div>
                            <div className="par d-flex justify-content-center">
                                <div>
                                    <p className="quick-des">
                                        <span>I'm a </span>
                                        <span className="sub-quickdes">
                                            <ElementTextRepeat speed={50} pauseTime={750}>
                                                {intro.speicialization}
                                            </ElementTextRepeat>
                                            </span>
                                    </p></div>
                            </div>
                            <div className="contact-sections">
                                <div className='d-flex justify-content-between align-items-center'>
                                    <Animated animationIn="bounceInLeft" animationInDelay={850}>
                                        <a className="a-tag-contact"
                                            href={intro.github} target="_blank" rel="noopener noreferrer">
                                            <i className="navbar-nav-svg fab fa-github-square"></i>
                                        </a>
                                    </Animated>
                                    <Animated animationIn="bounceInUp" animationInDelay={850}>
                                        <a className='a-tag-contact'
                                            href={intro.facebook} target="_blank" rel="noopener noreferrer">
                                            <i className="navbar-nav-svg fab fa-facebook-square"></i>
                                        </a>
                                    </Animated>
                                    <Animated animationIn="bounceInLeft" animationInDelay={850}>
                                        <a className='a-tag-contact'
                                            href={intro.linkedln} target="_blank" rel="noopener noreferrer">
                                            <i className="navbar-nav-svg fab fa-linkedin-in"></i>
                                        </a>
                                    </Animated>
                                    <Animated animationIn="bounceInDown" animationInDelay={900}>
                                        <a className='a-tag-contact' onClick={this.copyEmailAddress}
                                            href={"mailto:"+intro.email}>
                                            <i className="navbar-nav-svg fas fa-at"></i>
                                        </a>
                                    </Animated>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
                <div id="gmail-alert" className="alert alert-warning mt-3" role="alert">
                    Successful copy email to your clipboard.
                </div>
            </div>
        )
    }

}

export default Introduction;
