import { Component } from 'react';
import section from '../data/section.json';
import ElementTurn from '../../../../utils/components/elementTurn';
import { ReactBasicScroll } from "react-basic-scroll";
import Wow from 'wowjs';
import '../css/section.scss';

class Section extends Component {
    constructor(props){
        super(props);
        this.rawParameter = section;
        this.sectionClasses = '';
        this.initParameter();
        this.getRawParameter(null);
    }
    componentDidMount(){
        new Wow.WOW({
            live: false,
            offset: 150,
        }).init();
    }

    getRawParameter(params){
        if (params){
            let self = this;
            Object.keys(params).forEach(el=>{
                self[el] = params[el];
            })
        }
    }

    initParameter(){
        let self = this;
        Object.keys(this.rawParameter).forEach(el=>{
            self[el] = this.rawParameter[el];
        })
    }

    prepareRenderingElementHeader(){
        let turn = '';
        if (this.turnAnimate){
            turn = (<ElementTurn
                tagList={this.turnListIds || []}>
            </ElementTurn>)
        }
        let headerAnimationClass = '', wowDelay = '';
        let rotate = 'nostyle', rotateStyle='';
        if (this.headerStyle){
            rotate = 'style'
        }
        if (this.headerAnimateRotate){
            rotate = 'rotate';
            rotateStyle = `rotateX(var(${Object.keys(this.scrollConfig.props)[0]}))`
        }
        if (this.headerAnimation){
            headerAnimationClass = "title-content wow fadeInUp";
            wowDelay = '0.2s';
        }
        let header = (
            <div className={"header-shadow " + rotate} style={{transform: rotateStyle}}>
                <div className="convex-heading">
                    <div className={headerAnimationClass} data-wow-delay={wowDelay}>
                        <h3 className="display-4">
                            {this.sectionName}
                        </h3>
                        <div className='border-bottom'></div>
                        <div className="skills-geo  d-flex justify-content-center">
                            <div className="bg-outside">
                                {turn}
                            </div>
                        </div>
                    </div>
                </div>
            </div>)
        if (this.headerStyle){
            header = (
                <ReactBasicScroll config={this.scrollConfig}>
                        {header}
                </ReactBasicScroll>
            )
        }
        return header
    }
    prepareRenderingElementContent(){
        return (<div className={"container-fluid " + this.sectionContentClasses}></div>)
    }

    render() {
        let header = this.prepareRenderingElementHeader();
        let content = this.prepareRenderingElementContent();
        return (
        <div className={"section " + this.sectionClasses}>
            {header}
            {content}
        </div>
        )
    }
}

export default Section;
