import Section from '../../assets/components/section';
import staticData from './data/staticData.json';
import './css/about.scss';
import avatar from '../../assets/img/avatar.jpg';
import ElementTextWrite from '../../../../utils/components/elementTextWrite.js';


class About extends Section {
    setData() {
        return staticData
    }
    getRawParameter(params) {
        return super.getRawParameter(this.setData())
    }
    prepareRenderingElementContent() {
        return (<div className="page-content row d-flex align-items-center ">
            <div className="offset-md-1 col-md-3 wow fadeInUp" data-wow-delay="0.1s">
                <div className="cover-img">
                    <img src={avatar} alt="" className="container_img" />
                </div>
                <div className="quickview-info d-flex justify-content-between align-items-center">
                    <p className="birthday">
                        <i className="fas fa-birthday-cake"></i>
                        <span>{this.DOB}</span>
                    </p>
                    <p className="my-cv">
                        <a href="#/" target="_blank" rel="noopener noreferrer">
                            <i className="fas fa-cloud-download-alt"></i>
                            <span>My CV</span>
                        </a>
                    </p>
                </div>
            </div>
            <div className="contact-area col-md-4 wow fadeInUp" data-wow-delay="0.7s">
                <div className="contact-show" id="contact-area">
                    <p className="contact-title">CONTACT INFO</p>
                    <ul className="contact-list">
                        <li id="contact-add">
                            <i className="fas fa-map-marked-alt"></i>
                            <span>{this.address}</span>                                </li>
                        <a href={"tel:"+this.phone} id="contact-phone">
                            <i className="fas fa-mobile-alt"></i>
                            <span>{this.phone}</span>
                        </a>
                        <a href={"mailto:"+this.email} id="contact-email">
                            <i className="fas fa-envelope"></i>
                            <span>{this.email}</span>
                        </a>
                    </ul>
                </div>
                <div className="code-frame">
                    <ElementTextWrite speed={[20, 0]} execute={[]} >
                        <div className="coding-space linear-show">
                            <p className='code-line'>
                                <span className="code-var">Session</span>
                                <span className="code-opr"> = </span>
                                <span className="code-var">Website</span>
                                <span className="code-opr">.</span>
                                <span className="code-var">Authentication(
                                    <span className="string-var">'DrakeBui'</span>)
                                </span>
                            </p>

                            <p className='code-line'>
                                <span className="code-manage">try</span>
                                <span className="code-opr">:</span>
                            </p>
                            <div className="child-section">
                                <p className='code-line'>
                                    <span className="code-var">Connect</span>
                                    <span className="code-opr"> = </span>
                                    <span className="code-var">Session</span>
                                    <span className="code-opr">.</span>
                                    <span className="code-var">inquireInformation(
                                        <span className="string-var">'Bui Phi Long'</span>)
                                    </span>
                                </p>
                            </div>
                            <p className='code-line'>
                                <span className="code-manage">except</span>
                                <span className="code-built"> ResponseFailed</span>
                                <span className="code-control"> as</span>
                                <span className="code-built"> Error</span>:
                            </p>
                            <div className="child-section">
                                <p className='code-line'>
                                    <span className="code-manage">raise</span>
                                    <span className="code-built"> Error</span>(
                                    <span className="string-var">'System is in the rest time'</span>
                                    )
                                </p>
                            </div>
                            <p className="code-line">
                                <span className="code-manage">if</span>
                                <span className="code-var"> Connect</span>:
                            </p>
                            <div className="child-section">
                                <p className="code-line">
                                    <span className="code-var">Show(Contact.get(
                                        <span className="string-var">
                                            "Location"
                                        </span>
                                        ))</span>
                                    <var>#contact-add</var>
                                </p>
                                <p className="code-line">
                                    <span className="code-var">Show(Contact.get(
                                        <span className="string-var">
                                            "MobilePhoneNumber"
                                        </span>
                                        ))</span>
                                    <var>#contact-phone</var>
                                </p>
                                <p className="code-line">
                                    <span className="code-var">Show(Contact.get(
                                        <span className="string-var">
                                            "EmailAddress"
                                        </span>
                                        ))</span>
                                    <var>#contact-email</var>
                                </p>
                            </div>
                            <p className="code-line">StopSession(<span >Session.stop(<span className="string-var">'DrakeBui'</span>)</span>)</p>
                        </div>
                    </ElementTextWrite>
                </div>
            </div>
            <div className=" col-md-3 wow fadeInUp" data-wow-delay="0.4s">
                <div className="page-content-col" >
                    <p className="page-col-title">SUMMARY</p>
                    <div className="page-col-content">
                        <p>
                            The quote that can describe me is <b>"My life in my hand"</b>. 
                            Proactive and preparation is what I have done well to make my life follow long-term plans. 
                        </p>
                        <p className="">
                            As a Computer Scientist, I feel comfortable learning new technologies for updating myself.
                            For now, I have knowledge about <span className="emphasize">Website Development</span>, Analyze and Propose solutions for <span className="emphasize">ERP systems</span>. 
                            In addition, <span className="emphasize">Artificial Intelligence</span> also excited me.
                        </p>
                        <p>
                            Soft-skill, Research, Problem Solving are which I am rated well. In another field, I have really excited about gym, workout for improving my health.
                        </p>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default About;
