import Section from '../../assets/components/section';
import staticData from './data/staticData.json';
import './css/experiences.scss';
import HorizontalBadge from '../../assets/components/badgeHorizon.js'
import dynamicData from './data/dynamicData.js'


class Experience extends Section {
    setData() {
        return staticData
    }
    getRawParameter(params) {
        return super.getRawParameter(this.setData())
    }
    prepareRenderingElementContent() {
        this.dynamicData = dynamicData;
        if (this.dynamicData.length === 0){
            this.dynamicData = []
        }
        return (
            <div className="container-fluid " >
                <div className="hbadge-des-section row">
                    <div className="col-md-11">
                        {
                            this.dynamicData.map((el, ind) => <HorizontalBadge key={indexedDB} index={ind + 1} data={el} />)
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default Experience;
