import { Component } from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom';
import {ProfileSection} from '../../modules/profile/assets/components/export.js';


class MainAssets extends Component{
    render(){
        return (
            <Router>
              <Route path='/'><ProfileSection/></Route>
            </Router>
        )
    }
}

export default MainAssets;
