
import { Component, createRef } from 'react';
import '../css/horizontalBadgeNoFlip.scss';

export default class HorizontalBadgeNoFlip extends Component {
    constructor(props) {
        super(props)
        this.turnRef = createRef()
        this.animateSection = this.animateSection.bind(this)
        this.reAnimateSection = this.reAnimateSection.bind(this)
    }

    animateSection() {
        this.turnRef.current.classList.add('hover')
    }

    reAnimateSection() {
        this.turnRef.current.classList.remove('hover')
    }

    render() {
        return (
            <div className="hbadge-noflip-stage"
                onMouseEnter={this.animateSection}
                onMouseLeave={this.reAnimateSection}
            >
                <div className='row' >
                    <div className={"uni-section offset-md-1 col-md-4 wow " + this.props.data.animateLeft} data-wow-delay="0.2s">
                        <div className={"uni-stage circle-item item-number-" + this.props.index} ref={this.turnRef}>
                            <p>
                                {this.props.data.stage}
                            </p>
                        </div>
                        <div className="uni-title">
                            <div className="uni-major">
                                {this.props.data.position}
                            </div>
                            <div className="uni-name">
                                {this.props.data.subjectline}
                            </div>

                        </div>

                    </div>
                    <div className={"uni-acvm col-md-6 wow " + this.props.data.animateRight} data-wow-delay="0.2s">
                        <div className="uni-descript">
                            {this.props.data.description}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}