import Section from '../../assets/components/section';
import staticData from './data/staticData.json';
import './css/skill.scss';
import framework from './img/framework.png'
import platform from './img/platform.png'
import search from './img/search.png'


class Skill extends Section {
    setData() {
        return staticData
    }
    getRawParameter(params) {
        return super.getRawParameter(this.setData())
    }
    prepareRenderingElementContent() {
        return (
            <div className="container-fluid skill-sections">
                <div className=" row d-flex align-items-center ">
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="progl" data-wow-delay="0.1s">
                        <div className="subcol-icon">
                            <i className="fas fa-code"></i>
                        </div>
                        <div className="subcol-title">
                            PROGRAMMING LANGUAGES
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>Go</li>
                                <li>Python</li>
                                <li>Javascript</li>
                                <li>Other: C/C++/C#, Java, Scala</li>
                            </ul>
                        </div>
                    </div>
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="frwork" data-wow-delay="0.2s">
                        <div className="subcol-icon">
                            <i ><img alt="" src={framework} /></i>
                        </div>
                        <div className="subcol-title">
                            framework
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>Tensorflow, Pandas, Scikit-learn, Pytorch</li>
                                <li>Nodejs, Reactjs, OWL, Jquery</li>
                                <li>Flask, Django</li>
                                
                            </ul>
                        </div>
                    </div>
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="platform" data-wow-delay="0.2s">
                        <div className="subcol-icon">
                            <i ><img alt="" src={platform} /></i>
                        </div>
                        <div className="subcol-title">
                            platform
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>Odoo</li>
                                <li>Other: Docker, Vagrant, Nginx</li>
                                
                            </ul>
                        </div>
                    </div>
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="dbs" data-wow-delay="0.3s" >
                        <div className="subcol-icon">
                            <i className="fas fa-database"></i>
                        </div>
                        <div className="subcol-title">
                            Database
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>PostgreSql</li>
                                <li>Microsoft SQL Server</li>
                                <li>MongoDB, FireBase, Redis</li>
                            </ul>
                        </div>
                    </div>
                    
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="vctrl" data-wow-delay="0.3s" >
                        <div className="subcol-icon">
                            <i className="fab fa-git-alt" style={{ transform: 'rotateZ(90deg)', fontSize: '1.3em' }}></i>
                        </div>
                        <div className="subcol-title">
                        version control
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>Github, Gitlab</li>
                                <li>Data Version Control</li>
                            </ul>
                        </div>
                    </div>
                    <div className="page-subcol col-md-3 wow animate__animated animate__lightSpeedInRight" id="flangu" data-wow-delay="0.5s">
                        <div className="subcol-icon">
                            <i className="fas fa-language"></i>
                        </div>
                        <div className="subcol-title">
                            Languague
                        </div>
                        <div className="subcol-content">
                            <ul>
                                <li>English Communication</li>
                                <li>TOEIC: 815</li>
                            </ul>
                        </div>
                    </div>
                    <div className="page-subcol col-md-6 wow animate__animated animate__lightSpeedInRight" id="knowled" data-wow-delay="0.6s">
                        <div className="subcol-icon">
                            <i ><img alt="" src={search} /></i>
                        </div>
                        <div className="subcol-title knowledge" >
                            Other Knowledge
                        </div>
                        <div className="subcol-content">``
                            <ul>
                                <li>Understand about OOP, Design Pattern, SOLID principles, Dependency Injection.</li>
                                <li>Experience in  working with Agile process. Especially Scrum, Kanban.</li>
                                <li>Familiar with Restful API, RPC/gRPC with XML, JSON format.</li>
                                <li>Good at time management, presentation, teamwork skills.</li>
                                <li>Can work follow Quality at Speed rules, Jira, Atlassian</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Skill;
